.login {
	height: 100vh;
	background-color: white;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.login__logo {
	margin-top: 20px;
	margin-bottom: 20px;
	object-fit: contain;
	width: 100px;
	margin-right: auto;
	margin-left: auto;
}

.login__container {
	width:300px;
	height: fit-content;
	display: flex;
	flex-direction: column;
	border-radius: 5px;
	border: 1px solid lightgrey;
	padding: 20px;
}

.login__container > h1 {
	font-Weight: 500;
	margin-bottom: 20px;
}

.login__container > form >h5 {
	margin-bottom: 5px;
}

.login__container >form > input {
	height: 30px;
	margin-bottom: 10px;
	width: 98%;
	background-color: white;
	font-size: 17px;
    font-weight: 530;
}

.login__container >p {
	margin-top: 15px;
	font-size: 12px;
}

.login__signInButton {
	background: #f0c14b;
	border-radius: 2px;
	width: 100%;
	height: 30px;
	border: 1px solid;
	margin-top: 10px;
	border-color: #a88734 #9c7e31 #846a29;
}

.login__signInButton:active {
	background-color: #f0c14b;
  box-shadow: 0 3px lightgrey;
  transform: translateY(4px);
}

.login__registerButton {
	border-radius: 2px;
	width: 100%;
	height: 30px;
	border: 1px solid;
	margin-top: 10px;
	border-color: darkgrey;	
}






